<template>
   <nav>
       <v-snackbar v-model="snackbar" :timeout="4000" :top=true color="success">
           <span>Awesome! You added a new project</span>
           <v-btn text color="white" @click="snackbar=false">Close</v-btn>
       </v-snackbar>

        <v-app-bar app flat>
            <v-app-bar-nav-icon class="grey--text" @click="drawer = !drawer"></v-app-bar-nav-icon>
            <v-toolbar-title class="text grey--text">
                <Logo :branchName="this.currentUser.branch.name" :environment="this.currentUser.module.description"/> 
            </v-toolbar-title>
            <v-spacer></v-spacer>

            <v-btn @click="toggleTheme" small text>
                <v-icon centered>lightbulb</v-icon>
            </v-btn>

            <NotificationBar />  
           
            <v-menu offset-y>
                <template v-slot:activator="{ on }">
                    <v-btn text small color="grey" v-on="on" >
                        <v-icon centered>settings</v-icon>
                    </v-btn>
                </template>
                <v-list>
                    <v-list-item v-for="link in links" :key="link.text" router :to="`${link.router}`">
                        <v-list-item-icon>
                            <v-icon v-text="link.icon"></v-icon>
                        </v-list-item-icon>
                        <v-list-item-content>
                            <v-list-item-title class="font-weight-light">{{ link.text }}</v-list-item-title>
                        </v-list-item-content>
                    </v-list-item>
                </v-list>
            </v-menu>
            <v-btn text small color="grey">
                <v-icon>exit_to_app</v-icon>
            </v-btn>
       </v-app-bar>

        <v-navigation-drawer 
            v-model="drawer"
            app
            floating
            :permanent="drawer"
            :mini-variant.sync="mini" :color="this.$vuetify.theme.dark?'':'primary'" :dark="!this.$vuetify.theme.dark">
            
            <v-list-item class="px-2">
                <v-list-item-avatar>
                    <v-icon>mdi-account-outline</v-icon>
                </v-list-item-avatar>
                <v-list-item-content class="font-weight-light text-truncate">
                    {{this.currentUser.username}}
                </v-list-item-content>
                <v-btn icon small @click.stop="mini = !mini">
                    <v-icon>mdi-chevron-left</v-icon>
                </v-btn>
            </v-list-item>
            
            <v-divider></v-divider>
            <v-list v-for="s in sessions" :key="s.sessionName" >
                <v-subheader v-show="!mini" class="font-weight-light text-truncate">{{s.sessionName}}</v-subheader>
                <v-list nav>
                    <v-list-group 
                        v-for="m in s.menus"
                        :key="m.description"
                        v-model="m.active" color="white"
                        no-action>
                        <template v-slot:activator>
                            <v-list-item-icon>
                                <v-icon v-text="m.icon" class="text-truncate"></v-icon>
                            </v-list-item-icon>
                            <v-list-item-content>
                                <v-list-item-title class="font-weight-light text-truncate" v-text="m.description"></v-list-item-title>
                            </v-list-item-content>
                        </template>

                        <v-list-item v-for="submenu in m.submenus" link
                            :key="submenu.description" router :to="`/workspaces/${submenu.weblink}`">
                            <v-list-item-content>
                                <v-list-item-title class="font-weight-light text-truncate" v-text="submenu.description"></v-list-item-title>
                            </v-list-item-content>
                        </v-list-item>
                    </v-list-group>
                </v-list>
                <v-divider></v-divider>
            </v-list>
        </v-navigation-drawer>
   </nav>
</template>

<script>
import Logo from "./Logo" 
import NotificationBar from "./NotificationBar"
import {loggedUser } from '../mixins/loggedUser'

export default {
    components:{Logo,NotificationBar},
    mixins : [loggedUser],
    data(){
        return {
            drawer : false,
            links  : [
                {icon: 'account_circle', text: 'Profile'   , router:'/profile'},
                {icon: 'business'      , text: 'Company'   , router:'/company'},
            ],
            mini: true,
            snackbar : false,
            moduleId : 1,
            sessions : []
        }
    }, 
    computed : {
        buttonText() {
            return !this.$vuetify.theme.dark ? 'Go Dark' : 'Go Light'
        }
    },
    created() {
      this.$store.dispatch('workspace/loadSideBarMenu', 
                    {"languageId": this.currentUser.language.id,
                     "moduleId": this.currentUser.module.id}).then(
          menu => {
              this.sessions = menu.data.sessions
          },
          error => {
              console.log(error)
          }
      );
    },
    methods: {
        toggleTheme() {
            this.$vuetify.theme.dark = !this.$vuetify.theme.dark;      
        },
    },
}
</script>
